var timer = null
var curToken
import {
	uniSearchBar,
	uniIcons
} from '../../components/uni-icons/uni-icons.vue'
import {
	CALLRECORDLISTTODAY,
	SUBSCRIBEINFO,
	BANNER,
	SHOWINDEXTIP,
	INDEXCOUPON,
	JSAPI,
	PUSHCLICK
} from '../../utils/api.js'
import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
import uniNoticeBar from '../../components/uni-notice-bar/uni-notice-bar.vue'
import uniPopup from "../../components/uni-popup/uni-popup.vue"
var jweixin = require('jweixin-module')
export default {
	components: {
		uniSearchBar,
		uniNoticeBar,
		uniIcons,
		uniLoadMore,
		uniPopup
	},
	filters: {
		formateTel: function(value) {
			if (value == '057126211779') {
				var result = '魔小秘(示例电话)'
				return result
			} else {
				var pat = /(\d{3})\d*(\d{4})/
				var b = value.replace(pat, '$1****$2');
				return b
			}
		},
		formateDate: function(value) {
			var first = value.split(" ")[0]
			if (first !== undefined) {
				var second = first.split("-")
				var date = second[1] + "-" + second[2]
				return date
			}
		}
	},
	data() {
		return {
			todayStyle: 'border:1px solid #1c75ff;color:#1c75ff;box-shadow: 0 2rpx  10rpx #D4DDEC;',
			historyStyle: '',
			moduleData: [{
					src: '../../static/incomeHome/jietingpeizhi@2x.png',
					name: '接听配置'
				},
				{
					src: '../../static/incomeHome/lvsetongdao@2x.png',
					name: '绿色通道'
				},
				{
					src: '../../static/incomeHome/qianbao@2x.png',
					name: '我的钱包'
				}
			],
			bannerList: [],
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 3500,
			duration: 700,
			callList: [{}],
			getToken: "",
			itemData: "",
			todayText: 'more',
			page: 1,
			isfocus: '',
			imageshow: false,
			dueData: '',
			jp: '',
			pp: ''
		}
	},
	onLoad(options) {
		console.log('这是onload')
		// 获取jp
		this.getjp()
	},
	created() {
		console.log('这是created')
		// 是否关注
		this.isGuanzhu()
		// 获得JSSDK
		this.getJSAPI()
		this.getCurToken()
		// banner
		this.getBanner()
		// 初始化数据
		this.initData()
		// 获取会员状态
		this.getmember()
		uni.showToast({
			title: '正在加载中...',
			icon: 'none',
			mask: true,
			isfocus: ''
		})
	},
	onPullDownRefresh() {
		this.initData()
	},
	onReachBottom: function() {
		var _this = this
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_this.getmore();
		}, 1000);
	},

	methods: {
		// 获取jp
		getjp(){
			console.log('进入跳转判断')
			this.jp = uni.getStorageSync('myjp')
			this.pp = uni.getStorageSync('mypp')
			console.log('storage中的jp',this.jp)
			console.log('storage中的pp',this.pp)
			const params = {
				jp: this.jp
			}
			if(this.jp !== ''){
				this.$request.url_request(PUSHCLICK,params,'GET',res=>{},err=>{})
				// 支付页面
				if(this.jp == 13 || this.jp == 17 || this.jp == 9 || this.jp == 10){
					uni.navigateTo({
						url:'../MinePage/MineVipPage/MineVipPage'
					})
				} else if(this.jp == 8){
					// 配置页面
					uni.reLaunch({
						url:'../SettingPage/Setting/Setting'
					})
				} else if(this.jp == 1){
					// 通话详情
					if(this.pp !== ''){
						uni.navigateTo({
							url:'callDetails/callDetails?id='+this.pp
						})
					} else{
						uni.navigateTo({
							url:'callList'
						})
					}
				} else if(this.jp == 10001){
					uni.navigateTo({
						url:'../greenChannel/greenChannel'
					})
				}
				uni.removeStorageSync('myjp')
				uni.removeStorageSync('mypp')
			}
		},
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		},
		// 会员状态
		getmember() {
			var _this = this
			const params = {}
			this.$request.url_request(SHOWINDEXTIP, params, "GET", res => {
				this.dueData = JSON.parse(res.data).data
				if (this.dueData.show) {
					this.open()
				}
			}, err => {})
		},
		open() {
			this.$refs.popup.open()
		},
		closeDue() {
			this.$refs.popup.close()
		},
		// banner 弹窗关闭
		bannerClose(){
			this.$refs.bannerPop.close()
		},
		// 分享领取优惠券
		dueShare() {
			this.imageshow = true
			var that = this
			// 分享到朋友
			jweixin.onMenuShareAppMessage({
				title: '【魔小秘】您的专属智能来电助理', // 分享标题
				desc: '不想接，就挂断，来电助理帮你接听', // 分享描述
				link: 'https://m.checkshirt-ai.com/h5poster/index.html#/pages/banner/banner', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
				imgUrl: 'https://ai-assist.oss-cn-beijing.aliyuncs.com/aac/mxmlogo.png', // 分享图标
				type: 'link', // 分享类型,music、video或link，不填默认为link
				dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
				success: function() {
					// 用户点击了分享后执行的回调函数
					console.log('分享成功')
					that.imageshow = false
					that.$refs.popup.close()
					that.$refs.bannerPop.close()
					const params = {}
					that.$request.url_request(INDEXCOUPON, params, "GET", res => {
						uni.navigateTo({
							url:'../MinePage/MineVipPage/MineVipPage'
						})
					}, err => {})
				}
			})
			//分享到朋友圈
			jweixin.onMenuShareTimeline({
				title: '【魔小秘】您的专属智能来电助理', // 分享标题
				link: 'https://m.checkshirt-ai.com/h5poster/index.html#/pages/banner/banner',
				imgUrl: 'https://ai-assist.oss-cn-beijing.aliyuncs.com/aac/mxmlogo.png', // 分享图标
				success: function() {
					// 用户点击了分享后执行的回调函数
					that.imageshow = false
					that.$refs.popup.close()
					that.$refs.bannerPop.close()
					const params = {}
					that.$request.url_request(INDEXCOUPON, params, "GET", res => {
						uni.navigateTo({
							url:'../MinePage/MineVipPage/MineVipPage'
						})
					}, err => {})
				}
			})
		},
		// 是否关注
		isGuanzhu() {
			const params = {}
			this.$request.url_request(SUBSCRIBEINFO, params, 'GET', res => {
				this.isfocus = JSON.parse(res.data).data
			}, err => {})
		},
		// focus
		focus() {
			uni.navigateTo({
				url: 'focus/focus'
			})
		},
		// banner
		getBanner() {
			const params = {}
			this.$request.url_request(BANNER, params, 'GET', res => {
				this.bannerList = JSON.parse(res.data).data
				if (this.bannerList.length <= 1) {
					this.indicatorDots = false
				}
			}, err => {})
		},
		// gotoList今日历史记录
		gotoList() {
			uni.report('homeCallList', '来电记录')
			uni.navigateTo({
				url: 'callList'
			})
		},
		// 跳转banner链接
		bannerView(url, type,id) {
			console.log('点击banner')
			uni.report('homeBanner'+id, 'banner点击')
			if (type == 1) {
				// type 为1时跳转链接
				if (url) {
					window.location.href = url
				}
			} else if(type == 2){
				// type 为2时 打开领取优惠券
				this.$refs.bannerPop.open()
			} else if(type == 10002){
				// type 为10002时跳转到绿色通道
				uni.navigateTo({
					url:greenChannel
				})
			}

		},
		// 跳转模块
		gotoModule(index) {
			if (index == 0) {
				uni.report('homeSetting', '接听配置')
				uni.switchTab({
					url: '../SettingPage/Setting/Setting'
				})
			} else if (index == 1) {
				uni.report('homeGreen', '绿色通道')
				uni.navigateTo({
					url: '../greenChannel/greenChannel'
				})
			} else if (index == 2) {
				uni.report('homeWallet', '我的钱包')
				uni.navigateTo({
					url: '../MinePage/MineWallet/MineWallet'
				})
			}
		},
		// 截取
		getQueryString(name) {
			var after = window.location.search
			if (after.indexOf('?') === -1) {}
			after = window.location.href.split("?")[1] || after.substr(1);
			console.log('获取路径after', after)
			if (after) {
				var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
				var r = after.match(reg)
				if (r !== null) {
					console.log('截取的结果', decodeURIComponent(r[2]))
					return decodeURIComponent(r[2]);
				} else {
					return null
				}
			}
		},
		// 得到token
		getCurToken() {
			curToken = this.getQueryString('token')
			console.log('income中的token', curToken)
			if (!curToken) {
				//localstorage read
				curToken = uni.getStorageSync('myToken')
				console.log('locattion', curToken)
			}

			if (!curToken) {
				console.log('路径和storage的token都为空')
				uni.navigateTo({
					url: "../JumpLogin/JumpLogin"
				})
			} else {
				try {
					uni.setStorageSync('myToken', curToken)
				} catch (e) {
					//TODO handle the exception
					uni.showModal({
						content: e,
						showCancel: false
					});
				}
			}
		},
		// 获得JSAPI
		getJSAPI() {
			var that = this
			const params = {}
			this.$request.url_request(JSAPI, params, "GET", res => {
				this.jsData = JSON.parse(res.data).data
				console.log("得到的签名", this.jsData.signature)
				jweixin.config({
						appId: this.jsData.appId,
						timestamp: this.jsData.timestamp,
						nonceStr: this.jsData.nonceStr,
						signature: this.jsData.signature,
						jsApiList: ['updateAppMessageShareData', 'onMenuShareAppMessage', 'onMenuShareTimeline']
					}),
					jweixin.ready(function() {
						console.log("接口处理成功")
					}),
					jweixin.error(function() {
						console.log("接口处理失败")
					})
			}, err => {})
		},
		// 拨打电话
		call(tel) {
			uni.makePhoneCall({
				phoneNumber: tel
			})
		},
		// 初始化数据
		initData() {
			const params = {
				pageNum: 1
			}
			this.$request.url_request(CALLRECORDLISTTODAY, params, "GET", res => {
					this.itemData = JSON.parse(res.data).data.list
					uni.hideToast()
					uni.stopPullDownRefresh()
				},
				err => {});
		},
		// 加载更多
		getmore() {
			if (this.todayText == 'more') {
				this.todayText = 'loading'
				this.$forceUpdate()
				this.page++
				const params = {
					pageNum: this.page
				}
				this.$request.url_request(CALLRECORDLISTTODAY, params, "GET", res => {
					if (JSON.parse(res.data).data.list.length <= 0) {
						this.todayText = 'noMore'
						this.$forceUpdate()
					} else {
						this.todayText = 'more'
						this.itemData = this.itemData.concat(JSON.parse(res.data).data.list)
						this.$forceUpdate()
					}
				}, err => {})
			}
		},
		// 关闭遮罩
		close() {
			this.imageshow = false
		},
		gotoDetails(id) {
			uni.navigateTo({
				url: 'callDetails/callDetails?id=' + id
			})
		},

	}
}
